import { Component, OnInit } from '@angular/core';
import { UserService } from "../../../services/user/user.service";
import {FlashMessagesService} from "angular2-flash-messages";
import { AuthService} from "../../../services/auth/auth.service";
import { Router, ActivatedRoute} from "@angular/router";
import {MessageService} from "../../../services/message/message.service";
import  {ArrayService} from "../../../services/array/array.service";
import { Location } from '@angular/common';

@Component({
  selector: 'app-specific-user',
  templateUrl: './specific-user.component.html',
  styleUrls: ['./specific-user.component.css']
})
export class SpecificUserComponent implements OnInit {

  constructor(private userService: UserService, private flashMessage: FlashMessagesService, private arrayService: ArrayService,
              private authService: AuthService, private router: Router, private route: ActivatedRoute,
              private messageService: MessageService, private location: Location
  ) { }

  user: any;

  ngOnInit() {
    const username = this.route.snapshot.paramMap.get('username');
    this.userService.getSpecificUser(username).subscribe(data => {
      if(data.success){
        this.user = data.user;
      }
      else{
        this.router.navigate(['/home']);
        this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
      }
    }, err => {
      console.log(err);
      return false;
    });
  }

  goToAllUsers() {
    this.router.navigate(['/actions/allUsers']);
  }

}



