import { Injectable } from '@angular/core';

@Injectable()
export class ArrayService {

  constructor() { }

  //remove spesific user from a list
  deleteItemFromList(arrayOfItems, item){
    const index = arrayOfItems.indexOf(item);
    if(index != -1){
      arrayOfItems.splice(index, 1);
    }
  }
}



