import { Component, OnInit } from '@angular/core';
import { AuthService} from "../../../services/auth/auth.service";
import { FlashMessagesService} from "angular2-flash-messages";
import {UserService} from "../../../services/user/user.service";
import {Router} from "@angular/router";
import {MessageService} from "../../../services/message/message.service";
import {ValidateService} from "../../../services/validate/validate.service";

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {

  oldPassword: any;
  newPassword: any;

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router,  private messageService: MessageService,
    private userService: UserService
  ) { }

  ngOnInit() {
  }



  changePassword(){
    if(!this.validateService.validatePasswordForm(this.oldPassword, this.newPassword)){
      this.flashMessage.show('Enter old password and the new one', {cssClass: 'alert-danger', timeout: 3000});
    }
    else {
      this.userService.changePassword(this.oldPassword, this.newPassword).subscribe(data => {
        if(data.success){
          this.messageService.addMessage('Changing password').subscribe();
          this.flashMessage.show('The password has been changed. Please log in again', {cssClass: 'alert-success', timeout: 3000});
          this.messageService.addMessage('Logging out...').subscribe();
          this.authService.logout();
          this.router.navigate(['/login']);
        }
        else {
          this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
        }
      });
    }
    // this.authService.authenticateUser(user).subscribe(data => {
    //   if(data.success){
    //     this.authService.storeUserData(data.token, data.user);
    //     this.messageService.addMessage('Logging in').subscribe();
    //     this.flashMessage.show('You have been logged in', {cssClass: 'alert-success', timeout: 3000});
    //     this.router.navigate(['']);
    //   }
    //   else {
    //     this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
    //   }
    // });
  }

}



