import { Component, OnInit } from '@angular/core';
import { UserService } from "../../../services/user/user.service";
import {FlashMessagesService} from "angular2-flash-messages";
import { AuthService} from "../../../services/auth/auth.service";
import { Router} from "@angular/router";
import {MessageService} from "../../../services/message/message.service";
import  {ArrayService} from "../../../services/array/array.service";

@Component({
  selector: 'app-all-users',
  templateUrl: './all-users.component.html',
  styleUrls: ['./all-users.component.css']
})
export class AllUsersComponent implements OnInit {

  users: any = [];//from server


  constructor(private userService: UserService, private flashMessage: FlashMessagesService, private arrayService: ArrayService,
              private authService: AuthService, private router: Router, private messageService: MessageService
  ) { }

  ngOnInit() {
    this.userService.getAllUser().subscribe(data => {
      if(data.success){
        this.users = data.users;
        //sorting a list of users by alphabet
        this.users.sort((user1, user2) => {
          var un1 = user1.username, un2 = user2.username;
          if(un1 > un2)
            return 1;
          else if(un1 < un2)
            return -1;
          return 0;
        })
      }
      else{
          this.users = [];
          this.router.navigate(['/home']);
          this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
        }
    }, err => {
      console.log(err);
      return false;
    });
  }


  //
  onDeleteClick(userToDelete){
    console.log(this.users.indexOf(userToDelete));
    this.userService.deleteUser(userToDelete).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You have deleted the user: ' + userToDelete.username, {cssClass: 'alert-success', timeout: 3000});
        this.messageService.addMessage('Deleting user: ' + userToDelete.username).subscribe();
        this.arrayService.deleteItemFromList(this.users, userToDelete);
      }
      else{
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
      }
    });
  }

}



