//Modules
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule} from "@angular/http";
import { RouterModule , Routes} from '@angular/router';
//Components
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { AllUsersComponent } from './components/userActions/all-users/all-users.component';
import { MessageComponent } from './components/message/message.component';
import { ChangePasswordComponent } from './components/userActions/change-password/change-password.component';
import { SpecificUserComponent } from './components/userActions/specific-user/specific-user.component';
//Services
import {ValidateService} from "./services/validate/validate.service";
import  { AuthService } from "./services/auth/auth.service";
import {FlashMessagesModule} from "angular2-flash-messages";
import {AuthGuard} from "./guards/auth.guard";
import {AdminGuard} from "./guards/admin.guard";
import { UpdateComponent } from './components/userActions/update/update.component';
import {UserService} from "./services/user/user.service";
import {MessageService} from "./services/message/message.service";
import {ArrayService} from "./services/array/array.service";
//Routing
const appRoutes: Routes = [
  {path: 'home', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path: 'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  {path: 'actions/update', component: UpdateComponent, canActivate:[AuthGuard]},
  {path: 'actions/allUsers', component: AllUsersComponent, canActivate:[AuthGuard, AdminGuard]},
  {path: 'actions/change', component: ChangePasswordComponent, canActivate:[AuthGuard]},
  {path: 'actions/:username', component: SpecificUserComponent, canActivate:[AuthGuard, AdminGuard]},
  {path: 'messages', component: MessageComponent, canActivate:[AuthGuard]},
  {path: '**', redirectTo: '/home', pathMatch: 'full'}//The rest urls

];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    UpdateComponent,
    AllUsersComponent,
    MessageComponent,
    ChangePasswordComponent,
    SpecificUserComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule.forRoot()
  ],
  providers: [ValidateService, AuthService, AuthGuard, AdminGuard, UserService, MessageService, ArrayService],
  bootstrap: [AppComponent]
})
export class AppModule { }



