import { Component, OnInit } from '@angular/core';
import {FlashMessagesService} from "angular2-flash-messages";
import { Router} from "@angular/router";
import {UserService} from "../../services/user/user.service";
import {AuthService} from "../../services/auth/auth.service";
import {MessageService} from "../../services/message/message.service";
import {ArrayService} from "../../services/array/array.service";

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {

  constructor(private userService: UserService, private flashMessage: FlashMessagesService, private arrayService: ArrayService,
              private authService: AuthService, private router: Router, private messageService: MessageService) { }

  messages: any = [];

  ngOnInit() {
    this.messageService.getUsersMessages().subscribe(data => {
      if(data.success){
        this.messages = data.messages;
      }
      else{
        this.messages = [];
        this.flashMessage.show('There is no message added', {cssClass: 'alert-danger', timeout: 5000});
      }
    }, err => {
      console.log(err);
      return false;
    });
  }

  //delete chosen message
  onDeleteClick(msg){
    this.messageService.deleteOneMessage(msg).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You have deleted the message ', {cssClass: 'alert-success', timeout: 3000});
        this.arrayService.deleteItemFromList(this.messages, msg);
      }
      else{
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
      }
    });
  }
  //delete all messages
  onDeleteAllClick(){
    this.messageService.deleteAllMessages().subscribe(data => {
      if(data.success){
        this.flashMessage.show('You have deleted all the messages ', {cssClass: 'alert-success', timeout: 3000});
        this.messages = []
      }
      else{
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
      }
    });
  }

}



