import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {tokenNotExpired} from "angular2-jwt";

@Injectable()
export class AuthService {
  authToken: any;
  user: any;
  permission: any;

  constructor(private http: Http) { }

  registerUser(user){
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('/api/user/register', user, {headers: headers})
      .map(res => res.json());
  }
  authenticateUser(user){
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('/api/user/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  getProfile(){
    var headers = new Headers();
    //this.loadToken();
    headers.append('Authorization', this.getToken());
    headers.append('Content-Type', 'application/json');
    return this.http.get('/api/user/profile', {headers: headers})
      .map(res => res.json());
  }

  // loadToken(){
  //   const token = localStorage.getItem('id_token');
  //   this.authToken = token;
  // }

  getToken(){
    return localStorage.getItem('id_token');
  }

  getUser(){
    return JSON.parse(localStorage.getItem('user'));
  }



  // loadPermission(){
  //   this.getProfile().subscribe(profile => {
  //     this.permission = profile.user.permission;
  //   }, err => {
  //     return false;
  //   });
  // }


  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loggedIn(){
    return tokenNotExpired("id_token");
  }



  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

}



